import React, { memo } from 'react';
import { useDispatch } from 'react-redux';
import classnames from 'classnames/bind';
import { Text } from '@/_components/text';
import { setModalAction } from '@/services/redux/root-modules/notifications';
import styles from './index.module.scss';

const cn = classnames.bind(styles);

export const Page = memo(() => {
  const dispatch = useDispatch();

  const handleSetModalError = () =>
    dispatch(
      setModalAction({
        status: 'error',
        text:
          'test text test text test text test text test text test text test text test text test text test text test text test text ',
      }),
    );

  const handleSetModalSuccess = () =>
    dispatch(
      setModalAction({
        status: 'success',
        text:
          'test text test text test text test text test text test text test text test text test text test text test text test text ',
      }),
    );

  return (
    <div data-page="home" className={cn('homePage')}>
      <button type="button" onClick={handleSetModalError}>
        set modal error
      </button>
      <button type="button" onClick={handleSetModalSuccess}>
        set modal success
      </button>

      <Text text="hello boilerplate" size="h1" color="Black" />
    </div>
  );
});
